<template>
	<div class="device">
		<ul>
			<li>
				<p v-html='device'></p>
			</li>
			<li>
				<div>
					<p v-text='imgText'></p>
				</div>
				<figure>
					<figcaption v-text='desFrist'></figcaption>
					<img src="../../assets/home/1.png">
				</figure>
				<figure>
					<figcaption v-html='desSecond'></figcaption>
					<img src="../../assets/home/2.png" height="291" width="453">
				</figure>
			</li>
			<li class="doctor">
				<p v-html='doctor'></p>
			</li>
			
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				device:'Leksell伽玛刀历经六代发展，Perfexion系统是瑞典医科达公司在全球推出的具有革命创新意义的第六代伽玛刀，为目前世界上最先进的立体定向放射外科治疗设备。',
				imgText:'图1 和 图2 示PERFEXION伽玛刀设备和不同型号伽玛刀准直器比较。',
				desFrist:'图1.其治疗床在计算机的控制下也可进行上、下、左、右移动以及前进和后退，治疗精确度和安全性也得到了进一步提升。',
				desSecond:'图2.FERFEXION伽玛刀的准直器全部安装在伽玛刀的内部，无需人工更换准直器头盔。<br/>它有192个钴60放射源，准直器系统由原来的半球形改良为圆柱锥形状，圆柱内的治疗空间增大近3倍，治疗范围从颅内扩大到颅底、颅外的头颈部、颈部脊髓和鼻咽部。',
				doctor:'医生需要在Leksell GammaPlan上设计好治疗计划，传输到控制伽玛刀的计算机，将病人安放在治疗床上，治疗的全过程自动完成。新设备主机为双层防护设计，大大减少了工作中对医护人员和患者的辐射作用。伽玛刀治疗室内辐射水平相当低，患者接受的全身剂量是射波刀治疗时的1/100，第五代伽玛刀的1/20。所以，年幼的儿童患者及孕妇都可安全的接受Perfexion伽玛刀治疗，这一点对前几代伽玛刀及所有国产伽玛刀来说是很难想象的。<br />&nbsp;&nbsp;伽玛刀治疗的副作用一般极少出现。很少数病人出现伽玛刀的副作用：早期(数小时内)可能发生一过性头疼、头晕等放疗反应，经对症治疗可恢复。晚期(治疗半年后)并发病极为少见，多数患者可对症治疗恢复，很少数患者需手术治疗。除此以外，关于伽玛刀副作用治疗的并发病问题。与外科手术相比，伽玛刀副作用治疗后并发病出现的比例极低。'
				
			}
		}
	}
</script>

<style scoped>
	.device{
		margin-top: 0.2rem;  
	}
	ul{
		width: 96%;
		margin: auto;
	}
	ul li:nth-child(1),.doctor,.side{
		width: 80%;
		padding: 20px;
		line-height: 24px;
		margin: auto;
		color: #58B7FF;
		border: 1px solid #d3dce6;
		border-radius: 10px; 
	}
	.doctor,.side{
		margin-top: 0.6rem; 
	}
	ul li:nth-child(2){
		padding: 20px;
		width: 80%;
		margin: auto;
		color: #FF4949;
		margin-top: 0.2rem;
		border: 0.01rem dashed #d3dce6;
		border-radius: 10px; 
	}
	ul li:nth-child(2) div{
		margin-bottom: 0.3rem;
		text-align: center;
	}
	ul li figure{
		color: #324057;
		text-align: center;
	}
	ul li figure:nth-child(2){
		margin-bottom: 0.4rem;
	}
	ul li figure:nth-child(2) figcaption{
		box-sizing: border-box;
	}
	ul li figure figcaption{
		text-align: left;
		line-height: 24px;
	}
	ul li figure:nth-child(2) img{
		width: 230px;
		border-radius: 5px; 
		margin-top: 0.2rem; 
	}
	ul li figure:nth-child(3) img{
		width: 230px;
	}
</style>


