<template>
  <div class="hello">
    <div class='topImg'>
        <img src="../assets/home/top.jpg">
        <div class="logo">
          <img src="../assets/home/logo.png">
        </div>
        <div class="topTime" v-html='time'></div>
    </div>
    <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">伽玛刀治疗</el-menu-item>
        <el-menu-item index="2">专家介绍</el-menu-item>
        <el-menu-item index="3">设备介绍</el-menu-item>
        <el-menu-item index="4">视频介绍</el-menu-item>
    </el-menu>
    <router-view></router-view> 
  </div>
</template>

<script>
export default {
    data() {
      return {
        activeIndex: '1',
        time:''
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        if( key == 1 ){
          this.$router.push('cure')
        }else if( key == 2){
          this.$router.push('expert')
        }
        else if( key == 3 ){
           this.$router.push('device')
        }else{
          this.$router.push('vido')
        }
      },
      topTime(){
        let week = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
            localToday = '',
            d = new Date(),
            y = d.getFullYear(),
            m = d.getMonth()+1,
            dt = d.getDate(),
            h = d.getHours(),
            mt = d.getMinutes(),
            sd = d.getSeconds(),
            wk = week[d.getDay()];
        if( sd < 10){
          sd = '0'+sd;
        }
        if( mt < 10){
          mt = '0'+mt;
        };
        let localDay = y+'年'+m+'月'+dt+'日'+'&nbsp;'+h+':'+mt+':'+sd+'&nbsp;&nbsp;&nbsp;'+wk;
        this.time = localDay
        setTimeout(this.topTime,1000)
      },
      contactFc(){
        this.$router.push({path:'/hello/contact'})
      }
    },
    mounted(){
      this.topTime();
    }
  }
</script>

<style scoped>
.hello{
  padding-bottom: 40px;
}
.topImg{
  position: relative;
  width: 100%;
  height: 3.52rem;
}
.topImg>img{
  width: 100%;
  height: 3.52rem;
  display: block;
}
.logo{
  position: absolute;
  top: 0.4rem;
  left: 0.5rem;
  width: 3.3rem;
  height: 0.69rem;
}
.topImg>div img{
  width: 100%;
  display: block;
}
.el-menu--dark,.el-menu{
  padding: 0 0;
}
.el-menu--horizontal .el-menu-item{
  border-radius: 0rem;
  width: 25%;
  /*font-size: 0.14rem;*/
  font-size: 14px;
  text-align: center;

}

.topTime{
  position: absolute;
  bottom: 0.2rem;
  right: 0.3rem;
  font-size: 0.14rem;
  color: #bfcbd9;
  z-index: 999;
}
.el-button{
  position: absolute;
  top: 0.3rem;
  right: 0.6rem;
   text-align:center;
  border-radius: 4px;
  width: 1.5rem;
  padding: 5px;
}
</style>
<style>
   .el-menu--horizontal .el-menu-item{
   width: 140px;
  text-align: center;
}
</style>



