</template>

<script>
	export default{
		data(){
			return{
				lists:[
				{name:'王成伟主任:',number:'151-5316-9987'},
				{name:'石志钢:',number:'186-6061-2300'},
				{name:'微信:',number:'wjszg2008'},
				{name:'邮箱:',number:'2698244851@qq.com'},
				]
			}
		}
	}
</script>

<style scoped>
	.contact{
		margin: 0.2rem auto 0;
		border: 1px dashed #d3dce6;
		border-radius: 10px; 
		/*width: 80%;*/
		width: 6.4rem;
		padding: 20px 10px;
	}
	p{
		margin-bottom: 20px;
		color: red;
	}
	ul{
		margin: auto;
		text-align: center;
	}
	li{
		margin: 10px;
	}
	li span:nth-child(1){
	display: inline-block;
	width: 80px;
	text-align: right;
	}
	li span:nth-child(2){
	display: inline-block;
	width: 140px;
	text-align: left;
	}
</style>


