<script>
	export default{
		data(){
			return{
				content:"原发性三叉神经痛除了手术治疗，伽玛刀也是非常有效的治疗方式，而且创伤很小、费用较低。西班牙玛德里鲁韦尔国际医院功能神经外科和伽玛刀中心的研究人员回顾性探究伽玛刀治疗三叉神经痛的长期疗效，结果发表在2016年12月《Neurosurgery》杂志。153例难治性原发性三叉神经痛患者，伽玛刀治疗后随访24-171个月。该研究发现，117例随访患者中，Burchiel分级I-II级者达81%，完全有效；Burchiel分级I级者占52%，完全治愈，无需药物辅助治疗。3年完全治愈率78%-94%，平均85%；5年完全治愈率72%-91%，平均81%；7年完全治愈率65%-90%，平均76%。3年完全有效率86%-97%，平均91%；5年完全有效率为79%-93%，平均86%；7年完全有效率72%-93%，平均82%。伽玛刀治疗后疼痛缓解时间0-26.61个月，平均缓解时间3.36个月，中位数为2个月；27%患者在伽玛刀治疗当天疼痛消失。研究还发现：初始药物治疗有效的患者伽玛刀治疗效果比药物治疗无效者显著地好，疼痛缓解率分别是85.85%比69.7%（P=0.045）；未接受过任何手术治疗的患者对伽玛刀治疗的反应比接受过手术治疗者明显地高，疗效更好，其疼痛缓解率分别是87.5%比70.8%。<br /> &nbsp;&nbsp;&nbsp;Fukuoka等报告了1145例，随访(1-10年):5年或5年以上的疼痛完全缓解率为44%，药物辅助治疗下的疼痛完全缓解率为30%，疼痛控制不佳或疼痛复发者为18%；12%的病人面部感觉受影响，2%出现较严重的面部感觉异常；伽玛刀治疗使病人的生活质量明显提高，同时降低了药物治疗剂量；伽玛刀治疗后疼痛复发者仍可行微血管减压术。Murata报告了伽玛刀治疗110例三叉神经痛的长期疗效，同时分析了与疗效相关的因素:三叉神经痛的初期缓解率为93.4%，复发且需要其他治疗的比例为14.2%。治疗辐射剂量80-90Gy，中位剂量90Gy，平均剂量86.5Gy。<br /> &nbsp;&nbsp;&nbsp;伽玛刀并不是传统意义上的手术刀，而是一台精确放射治疗设备，并不进行开颅手术，是非常精确地将射线聚焦在颅内的病灶处，聚焦区内所接受的能量足以摧毁病灶，而不伤害周围的正常组织。伽玛刀是神经外科的亚学科---立体定向放射神经外科的代表性治疗设备。伽玛刀的出现奠定了放射神经外科发展的基础，极大的促进了放射神经外科的发展，拓宽了神经外科的治疗范围，使神经外科的学科发展进入到新的阶段---精准医疗，无创医疗的全新阶段。Leksell Perfexion伽玛刀利用立体定向聚焦的原理，将192束由钴60放射源释放的伽玛射线一次性集中照射到靶区（肿瘤、血管畸形等），使靶区组织接受较高的伽玛射线剂量，造成靶区组织细胞逐渐出现损伤及坏死，同时，伽玛射线剂量在靶区周围的脑组织中迅速锐减，极少或者不会造成正常脑组织发生意外伤害。伽玛刀治疗与传统的外科不同在于不用开颅，不需全身麻醉，损伤小，并发症少，治疗程序简便，费用相对低廉。<br /><span class='sp'>伽玛刀治疗的特点：</span><br />1、治疗过程基本无痛：仅仅在安装立体定向头架的时候需要对头皮行局部麻醉；<br />2、治疗周期短：一般情况下，只需接受一次伽玛刀治疗，一般2-3天；<br />3、日常生活影响小：伽玛刀治疗完成后立即拆除头架，无需禁食水；<br />4、治疗过程可控化：患者可以随时要求暂停治疗，在饮水、如厕及休息后再次开始余下的治疗。所以，伽玛刀治疗是一个无创、快捷、安全、舒适的治疗过程。<br /> &nbsp;&nbsp;&nbsp;伽玛刀治疗对于患者的全身条件要求较低，年龄可在5-80岁之间，具备基本生活自理能力，预期寿命超过半年以上即可。另外，由于Leksell Perfexion伽玛刀定位精准，对病灶周围脑组织及全身其它组织器官意外照射可能性极小，所以，年幼的儿童患者及孕妇都可安全的接受Perfexion伽玛刀治疗，这一点对前几代伽玛刀及所有国产伽玛刀来说是很难想象的。<br /><span class='sp'>山东大学第二医院伽玛刀治疗中心有以下几点优势：</span><br />1、国家卫计委直管的大型公立三甲医院，是目前山东省唯一拥有最新一代瑞典Leksell Perfexion头部专用伽玛刀的医院；<br />2、伽玛刀治疗中心为隶属神经外科管理的一个临床医技科室，依托神经外科及医院多学科会诊中心强大的专家团队，伽玛刀中心综合实力雄厚，所有医生均接受了国外医疗机构的培训，并与北京上海等地的知名专家保持密切学术联系；<br />3、治疗规范：所有患者都需要收治入院，严格掌握适应症，按照神经外科常规手术的要求进行术前准备，术前讨论及术后管理；<br />4、享受医保报销：伽玛刀治疗项目已纳入山东省医保报销目录，山东省患者在山东大学第二医院接受伽玛刀治疗时享受医保报销的福利。<br />5、费用低：尽管Leksell Perfexion伽玛刀设备价格昂贵，但收费仍执行与国产伽玛刀相同收费标准，治疗三叉神经痛的费用一般在1.5万元左右，远低于目前开颅手术收费。",
				one: '例1.肺癌多发脑转移瘤，全脑放疗结束后左颞和右顶病灶未缩小，于2016年1月20日在外院行伽玛刀治疗，2月后复查肿瘤未见缩小，3个月后病人左侧肢体活动不灵活，反应迟钝，记忆丧失，2016年5月9日复查MR示肿瘤明显增大，如图1显示。2016年5月16日经医生推荐来我院行第二次伽玛刀治疗，治疗后病人症状逐渐恢复正常，3月后复查MR示肿瘤明显缩小，见图2。',
