import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Vd from '../components/more/vd'
import Cure from '../components/more/cure'
import Device from '../components/more/device'
import Contact from '../components/more/contact'
import Expert from '../components/more/expert'

Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',redirect: '/hello/cure'},
    {
      path: '/hello',
      component: Hello,
      children:[
      {
        name:'vd',
      	path:'/hello/vido',
      	component: Vd
      },
      {
        name:'cure',
        path:'/hello/cure',
        component:Cure
      },
      {
        name:'device',
        path:'/hello/device',
        component:Device
      },
      {
        name:'expert',
        path:'/hello/expert',
        component:Expert
      }
      ]
    }
  ]
})



